<template>
	<div class="list">
		<div class="wrapper">
			<h1>ToDoList</h1>
			<div class="inputBox">
				<input class="inputStyle" type="text" v-model.trim="tempTxt" @keyup.enter="addList" placeholder="請輸入待辦事項">
				<div class="btn" @click="addList">確認</div>
			</div>
			<div class="tabBox">
				<div class="tab" :class="{active:visibility==='all'}" @click="visibility='all'">全部</div>
				<div class="tab" :class="{active:visibility==='active'}" @click="visibility='active'">未完成</div>
				<div class="tab" :class="{active:visibility==='complete'}" @click="visibility='complete'">已完成</div>
			</div>
			<ul class="listBox">
				<li v-for="(item,index) in filterList" :key="index">
					<!-- <input type="checkbox" :id="item.id"> -->
					<label :class="{completed:item.completed}" @click="listCompleted(item.completed,item.id)">
						{{item.text}}
					</label>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
	data () {
		return {
			tempTxt: '',
			visibility: 'all',
		}
	},
	computed:{
		filterList(){
			let tempList = [];
			if(this.visibility==='all'){
				return this.$store.state.list;
			}else if(this.visibility==='active'){
				this.$store.state.list.forEach((item) => {
					if(!item.completed){
						tempList.push(item);
					}
				});
				return tempList;
			}else if(this.visibility==='complete'){
				this.$store.state.list.forEach((item) => {
					if(item.completed){
						tempList.push(item);
					}
				});
				return tempList;
			}
		},
		// filterList(){
		// 	return this.$store.getters.list(this.visibility);
		// },
		// ...mapGetters(['list'])
	},
	methods:{
		addList: function(e){
			let date = new Date();
			let text = this.tempTxt;
			if(!text) return false;
			this.tempTxt = "";
			this.$store.dispatch('addList',{
				id: date.getTime(),
				text: text,
				completed: false 
			})
		},
		listCompleted:function(isCompleted,itemId){
			this.$store.dispatch('isCompleted',{isCompleted,itemId});
		}
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
	=btnStyle
		font-size: 16px
		display: inline-block
		border: 1px solid #ccc
		padding: 1px 2px
		cursor: pointer
		color: #666
		transition: .3s
		&:hover
			background-color: #eee
			color: #333
		
	*
		// outline: 1px solid #f00
		// padding: 0
		// margin: 0

	.wrapper
		max-width: 550px
		width: 100%
		margin: auto
		.inputBox
			.inputStyle
				font-size: 16px
				padding: 2px

			.btn
				+btnStyle
		.tabBox
			width: 100%
			margin-top: 20px
			font-size: 0
			.tab
				width: 33.333333%
				+btnStyle
				box-sizing: border-box
				&.active
					background-color: #eee
					color: #333
					


		.listBox
			width: 100%
			margin: auto
			padding: 0
			border: 1px solid #ccc
			border-top: none
			box-sizing: border-box
			li
				list-style: none
				padding: 5px 10px
				position: relative
				&:nth-child(odd)
					background-color: #eee
				input
					position: absolute
					left: 0
					top: 50%
					transform: translateY(-50%)
				label
					width: 90%
					display: block
					margin: auto
					

	.completed
		text-decoration: line-through
	
</style>



