import Vue from "vue";
import Router from "vue-router";

import Home from '@/components/HelloWorld';
import ListPage from '@/components/ListPage';

Vue.use(Router);

export default new Router({
    routes: [
        {
            name: "",
            path: "/",
            component: Home
        },
        {
            name: "todolist",
            path: "/todolist",
            component: ListPage
        },
    ]
});




