import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
	strict: true,
	state: {
		list:[
			{
				id: 1,
				text: 'list1',
				completed: true 
			},
			{
				id: 2,
				text: 'list2',
				completed: false 
			}
		]
	},
	actions:{
		addList(context,status){
			context.commit('LIST',status);
		},
		isCompleted(context,status){
			context.commit('COMPLETED',status);
		},
	},
	mutations:{
		LIST(state,payload){
			state.list.push(payload);
		},
		COMPLETED(state,{isCompleted,itemId}){
			state.list.find(function(item,index){
				if(item.id === itemId){
					item.completed = !isCompleted;
				}
			})
		}
	},
	getters:{
		// list: state=>state.list,
		list: (state) => (visibility) => {
			let tempList = [];
			if(visibility==='all'){
				return state.list;
			}else if(visibility==='active'){
				state.list.forEach((item) => {
					if(!item.completed){
						tempList.push(item);
					}
				});
				return tempList;
			}else if(visibility==='complete'){
				state.list.forEach((item) => {
					if(item.completed){
						tempList.push(item);
					}
				});
				return tempList;
			}
    }
	}
})




